<template lang="pug">
.bg-01
    canvas
</template>

<script>
export default {
    // name: 'bg'
}

let c = document.createElement('canvas')
let ctx = c.getContext('2d')

document.body.appendChild(c)

let w = c.width = window.innerWidth
let h = c.height = window.innerHeight
let tick = 0
let particles = []
let maxRadius = Math.sqrt(w * w / 4 + h * h / 4)

let fontArr = ['12px Handlee', '12px Roboto', '12px monospace']

// ctx.font = '12px Handlee'
// ctx.font = '12px Roboto'
// ctx.font = '12px monospace'
ctx.font = fontArr[Math.floor(Math.random() * 3)]

function Particle () {
    this.reset()
}
Particle.prototype.reset = function () {
    this.radian = Math.random() * Math.PI * 2
    this.radius = 0
    this.angSpeed = 0.01
    this.incSpeed = 0.8

    this.x = this.y = 0
}
Particle.prototype.step = function () {
    let prevX = this.x
    let prevY = this.y

    this.radian += this.angSpeed
    this.radius += this.incSpeed

    this.x = this.radius * Math.cos(this.radian)
    this.y = this.radius * Math.sin(this.radian)

    let dx = this.x - prevX
    let dy = this.y - prevY
    let len = Math.sqrt(dx * dx + dy * dy)

    for (let i = 0; i <= len; i += 10) {
        let y = prevY + dy * i / len
        let x = prevX + dx * i / len

        let posX = (x / 10 | 0) * 10
        let posY = (y / 10 | 0) * 10

        let a = posX / w * 240 + posY / h * 240 + tick
        let b = Math.random() * 360
        // let r = Math.random() < 0.5 ? 0 : 1
        let randomNum = Math.ceil(Math.random() * 25)
        // let r = Math.random() < 0.5 ? 65 : 97
        let az = String.fromCharCode(65 + randomNum)
        // let randNum = String.fromCharCode(33 + Math.ceil(Math.random() * 93))

        let ab = Math.random() * 2 < 1 ? a : b
        ctx.fillStyle = 'rgb(2,2,2)'
        ctx.fillRect(w / 2 + posX, h / 2 + posY - 9, 10, 10)
        ctx.fillStyle = 'hsl(hue,80%,50%)'.replace('hue', ab)
        ctx.fillText(az, w / 2 + posX, h / 2 + posY)
    }

    if (this.radius >= maxRadius) this.reset()
}

function anim () {
    window.requestAnimationFrame(anim)

    ++tick

    ctx.fillStyle = 'rgba(20, 20, 20, .03)'
    ctx.fillRect(0, 0, w, h)

    if (particles.length < 100 && Math.random() < 0.3) {
        particles.push(new Particle())
    }

    particles.map(function (particle) {
        particle.step()
    })
}
// ctx.fillStyle = '#000'
ctx.fillStyle = '#000'
ctx.fillRect(0, 0, w, h)
anim()

window.addEventListener('resize', function () {
    w = c.width = window.innerWidth
    h = c.height = window.innerHeight
    // ctx.font = '12px monospace'
    // ctx.font = '12px Handlee'
    ctx.font = fontArr[Math.floor(Math.random() * 3)]

    // ctx.fillStyle = '#000'
    ctx.fillStyle = '#000'
    ctx.fillRect(0, 0, w, h)

    maxRadius = Math.sqrt(w * w / 4 + h * h / 4)
})
</script>

<style lang="scss">
canvas {
    position: absolute;
    left: 0;
    top: 0;
    z-index: -2;
}
</style>



