<template lang="pug">
.menu
    menuIcon
    ul.menu-main(:class='{active: menuToggle}')
        menuItems(v-for='item in mName', :name='item')
</template>

<script>
// http://codepen.io/suez/pen/vAais
// http://www.html-js.com/article/3663
// https://vuex.vuejs.org/zh-cn/mutations.html
// import Vue from 'vue'
// import menuStore from 'store/menu'
import menuItems from './menu/menu-item'
import menuIcon from './menu/menu-icon'

// Vue.component('menu-item', menuItem)
export default {
    name: 'menu',
    data () {
        return {
            mName: [
                'home',
                'blog',
                'hazy', // 
                'about',
                'leisure',
                'github'
            ]
        }
    },
    computed: {
        menuToggle () {
            return this.$store.state.menuToggle
        }
    },
    components: {
        menuIcon,
        menuItems
    }
}
</script>

<style lang="scss">
@import '../sass/_part';

.menu {
    @extend %absolute-center;
    .menu-main {
        height: 86px;
        margin: -43px;
        .rotater a {
            opacity: 1;
        }
    }
}
$numEles: 6;

@for $i from 1 through $numEles {
    $deg: 360deg / $numEles;
    .rotater:nth-child(#{$i}) {
        transform: rotate($deg/-2 + $deg * ($i - 1));
    }
    .menu-main.active {
        .rotater {
            border: none;
        }
        .rotater:nth-child(#{$i}) a {
            transform: translateY(-10em) rotate($deg/2 - $deg * ($i - 1));
            @media screen and (max-width: 560px) {
                transform: translateY(-3em) rotate($deg/2 - $deg * ($i - 1));
            }
            width: 100%;
            height: 100%;
            opacity: 1;
            z-index: 1;
            transition: transform 1.5s, border 1.5s;
            border: solid 3px rgba(255, 255, 255, .8);
            border-radius: 50%;
            i.ico, span {
                opacity: 1;
            }
        }
    }
}
</style>



