<template lang="pug">
li.rotater
    a(v-if='name === "github"', href='https://github.com/lencx')
        i.ico(:class='name')
        span  {{name}}
        
    a(v-else-if='name === "hazy"', href='/todo')
        i.ico(:class='name')
        span  {{name}}
        
    a(v-else, :href='name')
        i.ico(:class='name')
        span  {{name}}
</template>

<script>
export default {
    name: 'menu-item',
    props: ['name'],
    data () {
        return {
            n: 'name'
        }
    },
    computed: {
        menuToggle () {
            return this.$store.state.menuToggle
        }
    }
}
</script>

<style lang="scss">
%pos-init {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

a {
    color: #fff;
}
ul {
    position: relative;
    margin: 0;
    padding: 0;
    li.rotater {
        width: 100%;
        height: 100%;
        @extend %pos-init;
        transform-origin: 50% 50%;
        box-sizing: border-box;
        border: solid 3px rgba(255, 255, 255, .3);
        // transition: border 1.5s cubic-bezier(0, 0.44, 0.48, 1.96);
        transition: border 1.5s cubic-bezier(0.41, 0.9, 0.57, 1.07);
        a {
            @extend %pos-init;
            top: -86px;
            width: 0;
            height: 0;
            text-align: center;
            text-decoration: none;
            line-height: 86px;
            cursor: pointer;
            // animation
            transition: transform 2s;
            transform: translateX(0);

            &:hover {
                background: rgba(200, 200, 200, .3);
                span {
                    font-weight: bold;
                }
            }

            .ico, span {
                transition: color 3s;
                color: #fff;
                font-weight: bold;
                opacity: 0;
                z-index: -10;
                cursor: pointer;
                transition: opacity 800ms, z-index 300ms, transform 1s;
                transform: translateX(0);
            }
            span {
                // display: none;
                font-family: 'Handlee';
                opacity: .1;
            }
            i:before {
                display: inline-block;
                width: 30px;
                height: 30px;
                content: '';
                vertical-align: -7px;
                // margin-right: 3px;
            }
            .home:before {
                background: url('../../assets/home.svg') 80%/cover;
            }
            .github:before {
                background: url('../../assets/github.svg') 80%/cover;
            }
            .blog:before {
                background: url('../../assets/blog.svg') 80%/cover;
            }
            .hazy:before {
                background: url('../../assets/hazy.svg') 80%/cover;
            }
            .leisure:before {
                background: url('../../assets/leisure.svg') 80%/cover;
            }
            .about:before {
                background: url('../../assets/about.svg') 80%/cover;
            }
        }
    }
}
</style>



