<template lang="pug">
.menu-bg(:class='{menuon: menuToggle}', @click='onMenu')
    .menu-icon
        span.icon-t
        span.icon-m
        span.icon-b
</template>

<script>
export default {
    computed: {
        menuToggle () {
            return this.$store.state.menuToggle
        }
    },
    methods: {
        onMenu () {
            this.$store.commit('onMenu')
        }
    }

}
</script>

<style lang="scss">
@import '../../sass/_base';
@import '../../sass/_animat';

@mixin init {
    &:after,
    &:before {
        @content;
    }
}
.menu-bg {
    position: absolute;
    background: rgba(255, 255, 255, .3);
    padding: 21px 18px;
    border-radius: 50%;
    cursor: pointer;
    margin: -43px;
    &:hover {
        animation: rotate 5s linear infinite;
    }
    z-index: 2;
}
.menu-icon {
    position: relative;
    width: 50px;
    height: 44px;
    transform: rotate(0);
    // transition: .5s cubic-bezier(0.445, 0.050, 0.550, 0.950);

    .icon-t,
    .icon-m,
    .icon-b {
        @include init {
            box-sizing: border-box;
            position: absolute;
            display: block;
            content: '';
            width: 25px;
            height: 10px;
            // background: #fff;
            border: solid 2px #fff;
            z-index: 1;
        }
        &:before {
            left: 0;
            @include border-left(4px);
            border-right: none;
        }
        &:after{
            right: 0;
            @include border-right(4px);
            border-left: none;
        }
    }
    .icon-t {
        @include init {
            top: 0;
        }
    }
    .icon-m {
        @include init {
            top: 17px;
        }
    }
    .icon-b {
        @include init {
            top: 34px;
        }
    }
}
.menuon {
    background: rgba(255, 0, 0, .2);
    transition: all 500ms ease;
    .icon-t,
    .icon-b {
        @include init {
            border-color: rgba(255, 255, 255, .8);
            transition: all 500ms ease;
        }
    }
    .icon-t {
        top: 15px;
        &:before {
            transform: rotate(45deg);
            top: 7px;
            left: 1px;
        }
        &:after {
            transform: rotate(-45deg);
            top: 7px;
            right: 1px;
        }
    }
    .icon-m {
        @include init {
            opacity: 0;
            transition: all 800ms ease;
        }
        &:before {
            transform: translateX(-18px);
        }
        &:after {
            transform: translateX(18px);
        }
    }
    .icon-b {
        &:before {
            transform: rotate(-45deg);
            top: 29px;
            left: 1px;
        }
        &:after {
            transform: rotate(45deg);
            top: 29px;
            right: 1px;
        }
    }
}
</style>



