<template lang="pug">
.logo
    span.whirl
</template>

<style lang="scss">
// @import '../sass/_anim';
$d: 4em;

@function stop-list($hover: false, $precision: 12) {
    $list: ();
    $unit: 360/$precision;
    @for $i from 0 through $precision {
        @if ($hover == true) {
            $list: $list,
                    hsl($i*$unit, 80%, 50%);
        }
        @else {
            $list: $list,
                    hsl($i*$unit, 100%, 75%);
        }
    }
    @return $list;
}

@mixin init-logo {
    position: absolute;
    margin: -.5*$d;
    left: 50%;
    top: 50%;
    width: $d;
    height: $d;
    border-radius: 50%;
}
.logo {
    display: inline-block;
    margin: 8px;
    position: relative;
    width: $d;
    height: $d;
    &:before {
        @include init-logo;
        content: '';
        z-index: 1;
        background: conic-gradient(stop-list());
        animation: whirl 2s linear infinite;
        box-shadow: 0 0 10px currentColor;
    }
    &:hover {
        &:before {
            background: conic-gradient(stop-list(true));
            animation: hoverWhirl 600ms linear infinite;
            box-shadow: 0 0 1em #fff;
            transition: all 300ms cubic-bezier(.42, 0, .58, 1);
        }
        .whirl {
            animation: whirl 5s linear infinite;
            box-shadow: 0 0 1em rgba(255, 221, 74, .8) inset;
        }
    }

    .whirl {
        @include init-logo;
        z-index: 2;
        box-sizing: border-box;
        background: url('/static/logo.png') 50%/cover padding-box #fff;
        border: solid 3px transparent;
        box-shadow: 0 0 5px rgb(0, 98, 114) inset;
    }
}

// @keyframes whirl {
//     to {
//         transform: rotate(1turn);
//     }
// }
@keyframes whirl {
    to {
        transform: rotate(1turn);
    }
}

@keyframes hoverWhirl {
    to {
        transform: rotate(-1turn);
    }
}
</style>

<script>
export default {
    name: 'logo'
}
</script>



