import Vue from 'vue'
import Vuex from 'vuex'
import {Style, info} from 'plugins/certificate'

console.log(`%c${info.len}%s`, Style, info.g)

Vue.use(Vuex)

export const state = {
    menuToggle: false
}

export const mutations = {
    // menu state
    onMenu (state) {
        state.menuToggle = !state.menuToggle
    }
}




